<template>
	<div class="user">
		<Banner :list="$store.state.User.Banner" collectionName="Banner"></Banner>
		
		<!-- <Cell v-for="(item, index) of $store.state.User" :list="item" :key="item.id" :index="index" collectionName="User"></Cell> -->
		<Cell v-for="(item, index) of cell" :list="item" :key="item.id" :index="index" collectionName="User"></Cell>
	</div>
</template>

<script>
import Banner from '../components/Banner/Banner.vue';
import Cell from '../components/Cell/Cell.vue';
import {mapState } from "vuex"
export default {
	name: 'user',
	props: {},
	mounted() {
		this.$store.dispatch('User/UPDATE_USER')
		this.$store.dispatch('User/UPDATE_BANNER')
	},
	components: {Banner,Cell},
	computed:mapState({cell:state=>state.User.User})
};
</script>

